@import url(http://fonts.googleapis.com/css2?family=Sora&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  font-family: Barlow;
  color: white;
}

/* Main body */
body {
  /* background: linear-gradient(217deg, rgba(255,0,0,.8), rgba(255,0,0,0) 70.71%),
            linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%); */
  background-image: url(/static/media/textureImage.ea0e2a13.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.bodyComponentLayout {
  -webkit-margin-start: 7%;
          margin-inline-start: 7%;
  -webkit-margin-end: 7%;
          margin-inline-end: 7%;
}
/* Navigation css*/
.MuiAppBar-positionStatic.MuiAppBar-positionStatic {
  background-color: transparent;
}

h1 {
  padding: 15px;
}

h6 {
  /* color: darkgoldenrod;  */
  color: #ecbd46;
  margin: 0px;
}
h3 {
  /* color: darkgoldenrod;  */
  color: #ecbd46;
}

/* */

/* Button css*/

.MuiButton-root.MuiButton-root {
  height: 36px;
  font-weight: 500;
  font-stretch: semi-condensed;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
}
.MuiTypography-body2.MuiTypography-body2 {
  color: "#a4a4a4";
}
/* footer+/ css*/
.makeStyles-footer-8.makeStyles-footer-8 {
  border-top: 1px solid #ffffff;
}

.MuiTypography-colorTextSecondary.MuiTypography-colorTextSecondary {
  color: #ffffff;
}
.MuiPaper-root.MuiPaper-root {
  background-color: black;
}
.IpadPro,
.mobile,
.IpadQuer {
  display: none;
}
.dash {
  width: 14%;
  margin-top: -20px;
}
.image {
  max-height: 350px;
}

/* media querry*/
.textHeaderColor {
  color: rgb 236 189 70;
}
/* .textColor {
  color: white;
} */
@media (max-width: 1366px) {
  .desktop,
  .mobile,
  .IpadQuer {
    display: none;
  }
  .IpadPro {
    display: block;
  }
}
@media (max-width: 1024px) {
  .desktop,
  .mobile,
  .IpadPro {
    display: none;
  }
  .IpadQuer {
    display: block;
  }
}
@media (max-width: 375px) {
  .desktop,
  .IpadPro,
  .IpadQuer {
    display: none;
  }
  .mobile {
    display: block;
  }
}

.text-price {
  position: absolute;
  top: 0px;
  right: 3%;
  width: 22%;
  height: 100%;
}
.newsletter-textfield {
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
  margin: 16px 0px !important;
  width: 100% !important;
}
